/*Footer*/
.Footer-container{
    width: 100%;
    height: 290px;
    background-color: rgba(255, 255, 255);
    padding-bottom: 1em;
    text-align: center;
    margin-top: 2em 
  }
  .Footer-container div{
    display:flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    
  }
  
  .icons{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    font-size: 24px;
    list-style: none;
    width: 300px;
    padding: 0;
   
  }
  
  #iconf{
    color:black;
    width:30px;
  }
  
  #iconf:hover{
    color:#9900F8;
  }
  