/*About*/
.About-container{
   display: flex;
   justify-content: center;
   align-items: center;
   width: 100%;
   border-bottom: 1px solid;
   border-color: #cccbcb;
   flex-direction: row;
   
  }
  .about-me, .skills{
    height: 100vh;
    width: 90%;
    color:white;
    border-right: 1px solid;
    border-left: 1px solid;
    border-top: 1px solid;
    border-color: #cccbcb;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   
  }
  .about-light, .skills-light{
    height: 100vh;
    width: 90%;
    color:rgb(49, 47, 47);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(233, 172, 172);
  }
  .skills-light{
    background-color: rgb(153, 154, 247);
  }
  .about-me h2, .about-light h2, .skills h2, .skills-light h2{
    width: 30%;
    font-size: 32px;
    
    text-align: center;
  }
  .about-me section, .about-light section{
    width: 50%;
   
  }   
  .p_about{
    font-size: 20px;
  }
  .email{
    background-color:rgb(221, 129, 113);
    padding: 1rem;
    border-radius: 5px;
  }

  #fa-envelope, #fa-download{
    color: #F0F413;
    font-size: larger;
  }
  #ClickMe{
    text-decoration: none;
    font-size: 20px;
    color: black;
   margin-left: 1rem;
  
  }
  .tools-icon{
    background-color: white;
    padding: 1rem;
    border-radius: 50%;
  }
  .resume-cv{
    background-color: rgb(206, 149, 149);
    padding: 1rem;
    border-radius: 10px;
  }
  .resume-cv:hover{
    background-color: rgb(179, 108, 108);
  }
  .quote{
    font-weight: 700;
    font-size: 20px;
  }

  @media screen and (max-width: 700px){
    .about-me, .skills,  .about-light, .skills-light{
      flex-direction: column;
      justify-content: center;
    }
    .about-me h2, .skills h2,  .about-light h2, .skills-light h2{
      font-size: 20px;
    }
    .p_about, .quote{
      font-size: 16px;
    }
    .about-me section, .about-light section{
      width: 80%;
    }
    .About-container{
      flex-direction: column;
    }
    #ClickMe{
      font-size: 16px;
    }
  }


  

  