.logo{
  font-size: 20px;
  font-weight: bold;
  margin-left: 2rem;
}
.theme-logo{
  font-size: 20px;
  margin-right: 2rem;
  border-radius: 20%;
  text-align:  center;
}

.fa-sun-o{
  background-color: rgb(86, 88, 88);
  padding: 1rem;
  border-radius: 50%;
  color: yellow;
  font-size: 20px;
  width: 16px;
  height: 16px;
  text-align: center;
}
.fa-moon-o{
  background-color: rgb(86, 88, 88);
  padding: 1rem;
  border-radius: 50%;
  color:#888;
  font-size: 20px;
  width: 16px;
  height: 16px;
  text-align: center;
}
.navbar{
  background-color: #121120;
  border-bottom: 1px solid;
  border-color: #cccbcb;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
}
.navbar-light{
 
  border-bottom: 1px solid;
  border-color: #cccbcb;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  align-items: center;
  
}
.home-cont{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  border-bottom: 1px solid;
  border-color: #cccbcb;
}
  .home-container{
    height: 100vh;
    width: 90%;
    border-right: 1px solid;
    border-left: 1px solid;
    border-color: #cccbcb;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap:wrap;

  }

  /*Left Container of home*/
  .right-container{
    width: 30%;
    padding: 2em;
    margin-left: 3em;
    background-color: #26273C;
    border-radius: 10px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .right-light{
    width: 30%;
    padding: 2em;
    margin-left: 3em;
    border-radius: 10px;
    text-align: center;
    background-color: #ababc9;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .label{
    margin-top: 4em;
  }
  .Kumusta{
    font-size: clamp(18px, 1.5vw, 36px);
   
  }
  .name{
    font-size: clamp(18px, 2vw, 38px);
    
  }
  .name-light{
    
  }
  /*new*/
  .web-dev{
    font-size: clamp(18px, 2vw, 38px);
    color: #d12828;
   
  }
  .contact{
    background-color: #9900F8;
    padding: 1em ;
    width: 14em;
    border-radius: 10px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    color:white;
  }
  .contact:hover{
   filter:brightness(60%);
  }
  /*Right image*/
  @keyframes expand{
    0% {width: 280px;}
    25% {width: 290px;}
    50%{transform: rotate(20deg);  }
    70%{transform: rotate(-20deg);}
    100%{width: 300px;}
  }
  @keyframes fade-in {
    0%{opacity: 100%;}
    10%{opacity: 20%;}
    40%{opacity: 40%;}
    60%{opacity: 60%;}
    80%{opacity: 80%;}
    100%{opacity: 100%;}
  }
  .Nicole{
    width:25rem;
    border-radius: 2em;
    animation: fade-in 5s;
  }

  @media screen and (max-width: 800px) {
    .Nicole{
      width: 15rem;   
    }
    .home-container{
      flex-direction: column;
    }
    .right-container, .right-light{
      width: 80%;
      margin: 0;
    }
  }
  
  
  
  