/*Projects*/
.my-projects-title{
    margin-top: 4em;
    font-size: 28px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    
  }
 
  .Projects-container{
    display: flex;
    flex-direction: row;
    width: 80%;
  }

  .left-arrow, .right-arrow{
    background-color: rgb(170, 5, 170);
    padding: 0.5rem;
    border-radius: 50%;
    margin: 2rem;
  }
  
  .node{
    background-color: #26273C;
    border-radius: 10px;
    padding: 2rem;
    transform: scale(0.8);
    text-align: center;
  }
  .node-light{

    background-color: #ffffff;
    color: black;
    border-radius: 10px;
    padding: 2rem;
    transform: scale(0.8);
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    text-align: center;
  }
  .node:hover, .node-light:hover{
    transform: scale(0.85);
    box-shadow: #c720ddf6 0px 6px 8px;
  }
  .proj-img {
    width: 100%;
    height: auto;
  }
  
  .proj-link{
    text-decoration: none;
    background-color: #9900F8;
    color:white;
    padding: 1em;
    border-radius: 10px;
    width: 260px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .proj-link:hover{
    filter: brightness(60%);
  }
  
  .Projects-main{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media screen and (min-width: 600px){
    .node{
     width: 20em;
    }
  }